<template>
  <div class="temporaryDishModify">
    <headerUsually btnShow="true" titleText="临时菜操作" url="/mobileOrdering/SDishOperation"></headerUsually>
    <div class="content">
      <div class="item name">
        <div class="left">名称</div>
        <div class="right">
          <input type="text" v-model="name" placeholder="请输入菜名" :maxlength="maxlength.length10" />
        </div>
      </div>
      <div class="item quantity">
        <div class="left">数量</div>
        <div class="right">
          <i class="iconfont icon-jian" @click="cutBackFun()"></i>
          <!-- <span class="num">{{quantity}}</span> -->
          <input class="num" type="text" v-model="quantity" />
          <i class="iconfont icon-jia2" @click="addFun()"></i>
        </div>
      </div>
      <div class="item amount">
        <div class="left">金额</div>
        <div class="right">
          <input type="text" v-model="price" placeholder="请输入单价" />
        </div>
      </div>
      <div class="item unit">
        <div class="left">单位</div>
        <div class="right">
          <input type="text" v-model="unit" placeholder="请输入单位" :maxlength="maxlength.length4" />
        </div>
      </div>
    </div>
    <div class="btn" @click="okFun()">确定</div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually
  },

  data() {
    return {
      isComponents: true,
      name: "",
      quantity: "1",
      price: "",
      unit: "",
      newModifySDish: {},
      newMyOrderInfo: {},
      maxlength: {
        length10: 10,
        length4: 4
      },
      originalNumber: 0
    };
  },
  computed: {
    ...mapState({
      modifySDish: state => state.mobileOrdering.modifySDish,
      myOrderInfo: state => state.mobileOrdering.myOrderInfo
    })
  },
  created() {
    console.log("modifySDish:", this.modifySDish);
    console.log("myOrderInfo:", this.myOrderInfo);
    this.newModifySDish = _.cloneDeep(this.modifySDish);
    this.newMyOrderInfo = _.cloneDeep(this.myOrderInfo);

    this.name = this.newModifySDish.descript;
    this.quantity = this.newModifySDish.number;
    this.price = this.newModifySDish.price;
    this.unit = this.newModifySDish.unit;
  },
  methods: {
    gobackFun() {
      this.$emit("emitHiddenAddTempDish");
    },
    cutBackFun() {
      if (this.selectedIndex > -1) {
        if (this.quantity > 0) {
          this.quantity--;
        }
      } else {
        if (this.quantity > 1) {
          this.quantity--;
        }
      }
    },
    addFun() {
      this.quantity++;
    },
    async okFun() {
      if (!this.name || !this.price || !this.unit) {
        this.msg("菜名、单价、单位，必填！");
      } else {
        if (this.newModifySDish.accnt) {
          let dcgateway = localStorage.getItem("dcgateway")
            ? JSON.parse(localStorage.getItem("dcgateway"))
            : {};
          let data = {
            params: {
              accnt: this.newModifySDish.accnt,
              type: this.newModifySDish.pluState,
              orderId: this.newModifySDish.id,
              descript: this.name,
              num: this.quantity,
              price: Number.parseFloat(this.price).toFixed(2),
              unit: this.unit
            }
          };

          let res = await ajaxGet(
            "/pos/router?method=updateCahngeDescriptOr",
            data
          );
          if (res && res.resultCode == 0) {
            this.$router.push("/mobileOrdering/myOrder");
          }
        } else {
          this.newModifySDish.amount =
            Number.parseFloat(this.price) * this.quantity;
          this.newModifySDish.number = this.quantity;
          this.newModifySDish.price = this.price;
          this.newModifySDish.descript = this.name;
          this.newModifySDish.unit = this.unit;
          //this.$store.commit("submitModifySDish", this.newModifySDish);
          this.$store.commit("changeTempTable", {
            type: "change",
            item: this.newModifySDish
          });

          this.$router.push("/mobileOrdering/myOrder");
        }
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  },
  watch: {
    quantity() {
      //console.log("val:", val);
      this.quantity = this.quantity.toString();
      if (Number(this.quantity) > 100) {
        this.quantity = this.originalNumber;
        return;
      }

      this.quantity = this.quantity.replace(/[^\d.]/g, ""); //清除“数字”和“.”以外的字符
      this.quantity = this.quantity.replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的
      this.quantity = this.quantity
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      this.quantity = this.quantity.replace(
        /^(\-)*(\d+)\.(\d\d).*$/,
        "$1$2.$3"
      ); //只能输入两个小数
      if (this.quantity.indexOf(".") < 0 && this.quantity != "") {
        //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
        this.quantity = parseFloat(this.quantity).toString();
      }
      this.originalNumber = this.quantity;
    }
  }
};
</script>
<style lang="less" scoped>
.temporaryDishModify {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9;
  top: 46px;
  background-color: #f6f8f9;
  .content {
    padding-left: 15px;
    background-color: #fff;

    .item {
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding-right: 15px;
      .left {
        font-size: 14px;
        color: #666666;
      }
      .right {
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #333333;
        input {
          text-align: right;
          outline: none;
          border: none;
        }
        .iconfont {
          color: #a8acb8;
        }
      }
    }
    .item:last-child {
      border-bottom: none;
    }
    .quantity {
      .right {
        .iconfont {
          color: #c4ad68;
          font-size: 30px;
        }
        .num {
          //margin: 0 20px;
          width: 50px;
          text-align: center;
          border: none;
          outline: none;
        }
      }
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



